.card {
	height: 180px;
	width: 150px;
	margin: 10px 20px;
	perspective: 1000px;
}

.card__inner {
	border: 3px solid white;
	height: 100%;
	position: relative;
	transition: transform 0.2s;
	transform-style: preserve-3d;
	transition-timing-function: cubic-bezier(0.01, 0, 1, 1);
	width: 100%;
}

.card--matched .card__inner {
	border-style: dotted;
}
.card--selected .card__inner{
	transform: rotateY(180deg);
}


.card__front, .card__back {
	backface-visibility: hidden;
	height: 100%;
	position: absolute;
	width: 100%;
}

.card__front {
	background-color: var(--secondary-bg);
}

.card__back {
	background-color: var(--accent);
	transform: rotateY(180deg);
}

/*Pineapple Pattern*/



