:root{
	--main-bg: #2a3132;
	--secondary-bg: #336b87;
	--light-accent: #90afc5;
	--accent: #763626;
	--base-text: white;
	--accent-darker: #52251a;
	--softPink: #F9D8D4;
	--yellow: #F9A734;
	--darkYellow: #FB8B24;
	--green: #36964C;
	--darkGreen: #286E38;
	--c : rgba(255,255,255,0.7);
  	--t : transparent;
}

body {
  background-color: var(--main-bg);
  color: var(--base-text);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
	font-family: Verdana;
}

button {
	background-color: var(--accent);
	color: var(--base-text);
	font-size: 15px;
	padding: 10px 15px;
	border-radius: 4px;
	cursor: pointer;
}

button:hover{
	background-color: var(--accent-darker);
}
