.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button-wrap {
  margin-bottom: 20px;
}

.button-wrap button {
  margin: 0 5px;
  width: 130px;
}

.button-wrap button:disabled {
  background-color: #838383;
  color: #b3b3b3;
  cursor: not-allowed;
}

.button-wrap button:disabled:hover {
  background-color: #838383;
}
